import React from 'react';
import PropTypes from 'prop-types';
import DropOption from '../common/DropOption';
import { Table, Modal } from 'antd';

const TraineeList = ({ showDetailModal, showModifyModal, ...tableProps }) => {
  const handleAction = (record, e) => {
    if (e.key === 'modify') {
      showModifyModal(record);
    } else if (e.key === 'delete') {
      Modal.confirm({
        title: ' [ ' + record.name + ' ] ',
        onOk() {},
      });
    } else if (e.key === 'recommend') {
    }
  };
  const columns = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      //render: (text, record) => <Link to={`/trainees/${record.id}`}>{text}</Link>,
      render: (text, record) =>
        <span>
          <a href="#" onClick={() => showDetailModal(record)}>
            text
          </a>
        </span>,
    },
    {
      title: '',
      dataIndex: 'sex',
      key: 'sex',
    },
    {
      title: '',
      dataIndex: 'age',
      key: 'age',
    },
    {
      title: '',
      dataIndex: 'guardian',
      key: 'guardian',
    },
    {
      title: '',
      dataIndex: 'phone',
      key: 'phone',
    },
    {
      title: '',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: '',
      width: 100,
      key: 'action',
      render: (text, record) =>
        <DropOption
          onMenuClick={e => handleAction(record, e)}
          menuOptions={[
            { key: 'modify', name: '' },
            { key: 'delete', name: '' },
            {
              key: 'recommend',
              name: '',
            },
          ]}
        />,
    },
  ];
  return (
    <Table
      bordered
      simple
      rowKey={record => record.id}
      columns={columns}
      {...tableProps}
    />
  );
};

TraineeList.propTypes = {
  showDetailModal: PropTypes.func,
  showModifyModal: PropTypes.func,
};

export default TraineeList;



