import React from 'react'
import PropTypes from 'prop-types'
import {Form, Input, DatePicker, Radio, Col, Row, Select} from 'antd';
import CSSModules from 'react-css-modules';
import styles from '../Trainee.scss';

const FormItem = Form.Item;
const TextArea = Input.TextArea;
const Option = Select.Option;

const DisabledInfo = ({
                        form: {
                          getFieldDecorator,
                          validateFields,
                          getFieldsValue,
                        }
                      }) => {
  return (
    <Form layout="horizontal" styleName="form-card">
      <FormItem label="" hasFeedback labelCol={{span: 6}}
                wrapperCol={{span: 6}}>
        {getFieldDecorator('disabled_type', {
          rules: [
            {
              required: true,
              message: ''
            },
          ],
        })(
          <Select style={{marginLeft: '5px'}}
                  placeholder=""
          >
          </Select>
        )}
      </FormItem>
      <FormItem label="" hasFeedback labelCol={{span: 6}}
                wrapperCol={{span: 6}}>
        {getFieldDecorator('disabled_level', {
          rules: [
            {
              required: true,
              message: ''
            },
          ],
        })(
          <Select style={{marginLeft: '5px'}}
                  placeholder=""
          >
          </Select>
        )}
      </FormItem>
      <FormItem label="" labelCol={{span: 6}}
                wrapperCol={{span: 12}}>
        {getFieldDecorator('disabled_level', {})(
          <TextArea style={{marginLeft: '5px'}}
                    placeholder="" autosize={{minRows: 3, maxRows: 3}}
          />
        )}
      </FormItem>
      <FormItem label="" labelCol={{span: 6}}
                wrapperCol={{span: 12}}>
        {getFieldDecorator('disabled_level', {})(
          <TextArea style={{marginLeft: '5px'}}
                    placeholder="" autosize={{minRows: 3, maxRows: 3}}
          />
        )}
      </FormItem>
    </Form>
  );
};

DisabledInfo.propTypes = {};

export default Form.create()(CSSModules(DisabledInfo, styles));



