import React from 'react';
import { Card, Row, Table } from 'antd';
import styles from './Occupation.scss';
import CSSModules from 'react-css-modules';

const OccupationInfo = ({}) => {
  const columns = [
    {
      title: '',
      dataIndex: 'id',
      key: 'id',
      width: '25%',
    },
    { title: '', dataIndex: 'ability', key: 'ability', width: '45%' },
    {
      title: '',
      dataIndex: 'criterion_score',
      key: 'criterion_score',
      width: '30%',
    },
  ];
  return (
    <div className="content-inner" styleName="detail" >
      <Row>
        <h2></h2>
        <p styleName="desc">
          
        </p>
      </Row>
      <Row>
        <Card
          title={
            <span>
               (<span styleName="necessary"></span>):
            </span>
          }
          bordered={false}
          noHovering="false"
        >
          <Table
            bordered={false}
            simple
            pagination={false}
            rowKey={record => record.id}
            columns={columns}
          />
        </Card>
      </Row>
      <Row>
        <Card
          title={
            <span>
               (<span styleName="secondary"></span>):
            </span>
          }
          bordered={false}
          noHovering="false"
        >
          <Table
            bordered={false}
            simple
            pagination={false}
            rowKey={record => record.id}
            columns={columns}
          />
        </Card>
      </Row>
    </div>
  );
};
export default CSSModules(OccupationInfo, styles);



