import React from 'react';
import PropTypes from 'prop-types';
import { Form, Input, DatePicker, Radio, Col, Row, Select } from 'antd';
import CSSModules from 'react-css-modules';
import styles from '../Trainee.scss';

const FormItem = Form.Item;
const TextArea = Input.TextArea;
const Option = Select.Option;

const BasicInfo = ({
  form: { getFieldDecorator, validateFields, getFieldsValue },
}) => {
  return (
    <Form layout="horizontal" styleName="form-card">
      <FormItem
        label="  "
        hasFeedback
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 4 }}
      >
        {getFieldDecorator('name', {
          rules: [
            {
              required: true,
              message: '',
            },
          ],
        })(<Input />)}
      </FormItem>
      <FormItem label="  " labelCol={{ span: 6 }} wrapperCol={{ span: 6 }}>
        {getFieldDecorator('sex', {
          initialValue: 'M',
        })(
          <Radio.Group>
            <Radio value="M"></Radio>
            <Radio value="F"></Radio>
          </Radio.Group>,
        )}
      </FormItem>
      <FormItem
        label=""
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 9 }}
        hasFeedback
      >
        {getFieldDecorator('id_card', {
          rules: [
            {
              required: true,
              message: '',
            },
          ],
        })(<Input placeholder="" />)}
      </FormItem>
      <FormItem label="" labelCol={{ span: 6 }} wrapperCol={{ span: 6 }}>
        {getFieldDecorator('birthday', {
          rules: [
            {
              required: true,
              message: '',
            },
          ],
        })(<DatePicker />)}
      </FormItem>
      <Row>
        <Col span={6} offset={4}>
          <FormItem
            label=""
            hasFeedback
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 16 }}
          >
            {getFieldDecorator('guardian', {
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            })(<Input />)}
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem wrapperCol={{ span: 12 }}>
            {getFieldDecorator('relationship', {})(
              <Select placeholder="" style={{marginLeft:'5px'}}>
                <Option value="male"></Option>
                <Option value="female"></Option>
              </Select>,
            )}
          </FormItem>
        </Col>
      </Row>
      <FormItem
        label=""
        hasFeedback
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 9 }}
      >
        {getFieldDecorator('phone', {
          rules: [
            {
              required: true,
              message: '',
            },
          ],
        })(<Input placeholder="" />)}
      </FormItem>
      <FormItem
        label="  "
        hasFeedback
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 12 }}
      >
        {getFieldDecorator('address', {})(
          <TextArea
            autosize={{ minRows: 2, maxRows: 2 }}
            placeholder=""
          />,
        )}
      </FormItem>
    </Form>
  );
};

BasicInfo.propTypes = {};

export default Form.create()(CSSModules(BasicInfo, styles));



