import React from 'react';
import PropTypes from 'prop-types';
import {
  Form,
  Input,
  DatePicker,
  Radio,
  Col,
  Row,
  Select,
  Switch,
  Card,
} from 'antd';
import CSSModules from 'react-css-modules';
import styles from '../Trainee.scss';

const FormItem = Form.Item;
const TextArea = Input.TextArea;
const Option = Select.Option;

class BackgroundInfo extends React.Component {
  state = {
    haveTrained: false,
    isWorked: false,
  };

  changeHaveTrained(checked) {
    this.setState({ haveTrained: checked });
  }

  changeIsWorked(checked) {
    this.setState({ isWorked: checked });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="horizontal" styleName="form-card">
        <Card title="" bordered={false} noHovering="false">
          <FormItem
            label=""
            hasFeedback
            labelCol={{ span: 6 }}
            wrapperCol={{ span: 6 }}
          >
            {getFieldDecorator('degree', {
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            })(<Select placeholder="" />)}
          </FormItem>
          <FormItem
            label=""
            hasFeedback
            labelCol={{ span: 6 }}
            wrapperCol={{ span: 6 }}
          >
            {getFieldDecorator('education_level', {
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            })(<Select placeholder="" />)}
          </FormItem>
          <FormItem label="" labelCol={{ span: 6 }}>
            <Switch
              defaultChecked={false}
              key="have_trained"
              onChange={checked => this.changeHaveTrained(checked)}
            />
          </FormItem>
          {this.state.haveTrained &&
            <FormItem
              label=""
              labelCol={{ span: 6 }}
              wrapperCol={{ span: 12 }}
            >
              {getFieldDecorator('trained_intro', {})(
                <TextArea
                  placeholder=""
                  autosize={{ minRows: 2, maxRows: 3 }}
                />,
              )}
            </FormItem>}
        </Card>
        <Card title="" bordered={false} noHovering="false">
          <FormItem label="" labelCol={{ span: 6 }}>
            <Switch
              defaultChecked={false}
              key="is_worked"
              onChange={checked => this.changeIsWorked(checked)}
            />
          </FormItem>
          {this.state.isWorked &&
            <FormItem
              label=""
              labelCol={{ span: 6 }}
              wrapperCol={{ span: 12 }}
            >
              {getFieldDecorator('worked_intro', {})(
                <TextArea
                  placeholder=""
                  autosize={{ minRows: 2, maxRows: 3 }}
                />,
              )}
            </FormItem>}
          <FormItem
            label=""
            labelCol={{ span: 6 }}
            wrapperCol={{ span: 12 }}
          >
            {getFieldDecorator('expect_work', {})(
              <TextArea
                placeholder=""
                autosize={{ minRows: 2, maxRows: 3 }}
              />,
            )}
          </FormItem>
        </Card>
        <Card title="" bordered={false} noHovering="false">
          <Row>
            <Col span={8}>
              <FormItem
                label="  "
                hasFeedback
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 14 }}
              >
                {getFieldDecorator('father', {
                  rules: [
                    {
                      required: true,
                      message: '',
                    },
                  ],
                })(<Input />)}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                label=""
                hasFeedback
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 14 }}
              >
                {getFieldDecorator('father_degree', {})(
                  <Select placeholder="" />,
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                label=""
                hasFeedback
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 14 }}
              >
                {getFieldDecorator('father_job', {})(
                  <Select placeholder="" />,
                )}
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span={8}>
              <FormItem
                label="  "
                hasFeedback
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 14 }}
              >
                {getFieldDecorator('mother', {
                  rules: [
                    {
                      required: true,
                      message: '',
                    },
                  ],
                })(<Input />)}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                label=""
                hasFeedback
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 14 }}
              >
                {getFieldDecorator('mother_degree', {})(
                  <Select placeholder="" />,
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                label=""
                hasFeedback
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 14 }}
              >
                {getFieldDecorator('mother_job', {})(
                  <Select placeholder="" />,
                )}
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <FormItem
                label=""
                labelCol={{ span: 3 }}
                wrapperCol={{ span: 13 }}
              >
                {getFieldDecorator('parent_expect', {})(
                  <TextArea
                    placeholder=""
                    autosize={{ minRows: 2, maxRows: 3 }}
                  />,
                )}
              </FormItem>
            </Col>
          </Row>
        </Card>
      </Form>
    );
  }
}

BackgroundInfo.propTypes = {};

export default Form.create()(CSSModules(BackgroundInfo, styles));



