import React from 'react';
import PropTypes from 'prop-types';
import { Form, Input } from 'antd';
import CSSModules from 'react-css-modules';
import styles from '../Occupation.scss';

const FormItem = Form.Item;
const TextArea = Input.TextArea;

const BasicInfo = ({
  occupation,
  onChange,
  form: { getFieldDecorator, validateFields, getFieldsValue },
}) => {
  return (
    <Form layout="horizontal" styleName="form">
      <FormItem
        label=""
        hasFeedback
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 10 }}
      >
        {getFieldDecorator('name', {
          initialValue: occupation.name,
          rules: [
            {
              required: true,
              message: '',
            },
          ],
        })(<Input />)}
      </FormItem>
      <FormItem label="" labelCol={{ span: 6 }} wrapperCol={{ span: 16 }}>
        {getFieldDecorator('description', {
          initialValue: occupation.description,
        })(
          <TextArea
            placeholder=""
            autosize={{ minRows: 5, maxRows: 5 }}
          />,
        )}
      </FormItem>
    </Form>
  );
};

BasicInfo.PropTypes = {
  isCreate: PropTypes.func,
  onChange: PropTypes.func,
  occupation: PropTypes.object,
};

export default Form.create({
  onFieldsChange: (comp, formItem) => comp.onChange(formItem),
})(CSSModules(BasicInfo, styles));



