import React, {Component} from 'react';
import PropTypes from 'prop-types';
import TraineeList from './TraineeList';
import TraineeFilter from './TraineeFilter';
import TraineeInfo from './TraineeInfo';
import {Modal} from 'antd';
import TraineeCreateSteps from './steps';

class Trainee extends Component {
  static defaultProps = {};

  constructor(props) {
    super(props);
    this.state = {
      modalMode: 'CLOSE'
    };
    this.showCreateModal = this.showCreateModal.bind(this);
    this.showModifyModal = this.showModifyModal.bind(this);
    this.showDetailModal = this.showDetailModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  /**
   * 
   */
  showCreateModal() {
    this.setState({
      modalMode: 'CREATE',
    });
  }

  /**
   * 
   */
  showModifyModal() {
    this.setState({
      modalMode: 'MODIFY',
    });
  }

  /**
   * 
   */
  closeModal() {
    this.setState({
      modalMode: 'CLOSE',
    });
  }

  /**
   * 
   */
  showDetailModal(record) {
    this.setState({
      modalMode: 'DETAIL',
    });
  }

  render() {
    const dataSource = [
      {
        id: '1001',
        name: '',
        sex: 'M',
        age: '10',
        guardian: '',
        phone: '13990909090',
        address: '',
      },
      {
        id: '1002',
        name: '',
        sex: 'F',
        age: '12',
        guardian: '',
        phone: '13980808080',
        address: '',
      },
    ];
    return (
      <div className="content-inner">
        <TraineeFilter showCreateModal={this.showCreateModal}/>
        <TraineeList
          dataSource={dataSource}
          showModifyModal={this.showModifyModal}
          showDetailModal={this.showDetailModal}
        />
        {(this.state.modalMode === 'CREATE' || this.state.modalMode === 'MODIFY') &&
        <Modal
          width={800}
          visible
          onCancel={this.closeModal}
          maskClosable={false}
          footer={null}
          title={this.props.isCreate ? "" : ""}
        >
          <TraineeCreateSteps isCreate={this.state.modalMode === 'CREATE'}/>
        </Modal>}
        {this.state.modalMode === 'DETAIL' &&
        <Modal
          width={630}
          visible
          maskClosable
          footer={null}
          onCancel={this.closeModal}
          title=""
        >
          <TraineeInfo/>
        </Modal>}
      </div>
    );
  }
}

export default Trainee;



